let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let header = document.querySelector(".header")
let navLinks = document.querySelectorAll(".nav__link")

if (burger) {
    burger.onclick = function() {
        header.classList.toggle("_open")
        body.classList.toggle("fixed-body")
    }
}

navLinks.forEach(link => {
    link.onclick = function(){
        header.classList.remove("_open")
        body.classList.remove("fixed-body")
    }

})

window.addEventListener('DOMContentLoaded', () => {
    animation()
})


function animation(){
    gsap.registerPlugin(ScrollTrigger);

    let whatGroups = document.querySelectorAll(".what-group")

    whatGroups.forEach(group => {
        const tlWhat = gsap.timeline({
            scrollTrigger: {
                trigger: group,
                start: 'top center',
                end: 'bottom center',
                toggleActions: 'play reverse play reverse'
            }
        })
        tlWhat.to(group.querySelector('.what-group__title'), {
            color: '#6FFE95'
        })
        tlWhat.to(group.querySelector('.what-group__desc'), {
            color: '#FFF'
        },'<')
    })
}
